img {
  width: 100%;
}

.landing {
  position: relative;
  background: url('./img/evea.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.card1 {
  border: 3px solid #f3f3f3;
  box-shadow: 0 1px 3px rgba(0,0,0,.253)!important;
  -webkit-transition: all .3s linear;
  transition: all .3s linear;
  padding: 10px;
  max-width: 800px;
  text-align: left;
  margin: 0 auto;
  width: 62rem;
}

.card-form {
  opacity: 0.9;
}
.card1-body1{
  padding: -2rem!important;
}
.latest-profiles-img {
  width: 40px;
  height: 40px;
}

.form-control::placeholder {
  color: #bbb !important;
}
.card-style {
  max-width: 3000%;
}
.card-body {
  min-height: 300px;
  min-width: 300px;
  margin-right: 5px;
}
